import React from 'react';
import styled from 'styled-components';
import Heading from '../../Heading';
import Paragraph from '../../Paragraph';

const IndividualProgramSection = props => {
  return (
    <Wrap>
      <ComponentHeading>
        <Heading size="2.8">
           
        </Heading>
        <Paragraph size="1">
                 ,    
        </Paragraph>
      </ComponentHeading>
      <ItemsWrap>
        <ComponentItem>
          <Heading size="1.8">
             
          </Heading>
          <Paragraph size="1">
                  .  ,    
          </Paragraph>
        </ComponentItem>

        <ComponentItem>
          <Heading size="1.8">
             
          </Heading>
          <Paragraph size="1">
                             
          </Paragraph>
        </ComponentItem>
        <ComponentItem>
          <Heading size="1.8">
            
          </Heading>
          <Paragraph size="1">
                    : ,    , .         ,     
          </Paragraph>
        </ComponentItem>
        <ComponentItem>
          <Heading size="1.8">
             
          </Heading>
          <Paragraph size="1">
               ,       .     .
          </Paragraph>
        </ComponentItem>
        <ComponentItem>
          <Heading size="1.8">
            
          </Heading>
          <Paragraph size="1">
                       .  ,    ,       , ,  ,      ,   
          </Paragraph>
        </ComponentItem>
      </ItemsWrap>
    </Wrap>
  )
}
const ComponentItem = styled.div`
  box-shadow: 0 3px 3px rgba(69, 83, 99, 0.2);
  border-radius: 8px;
  background-color: #ffffff;
  padding: 16px 20px;
  width: 30%;
  display: inline-block;
  box-sizing: border-box;
  margin: 5px 5px;
  width: 100%;
  max-width: 350px;
  text-align: left !important;
  direction: ltr;
  h1 {
    margin-bottom: 16px;
    line-height: 1em;
  }
  p {
    max-width: 260px;
  }
  
`

const ItemsWrap = styled.div`
  column-count: 3;
  column-gap: 5px;
  column-width: 30%;
  margin-top: 32px;
  direction: rtl;
  
  /* align-items: center; */
  @media(max-width: 768px) {
    column-count: 2;
  }
  @media(max-width: 600px) {
    column-count: none;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
`

const ComponentHeading = styled.div`
  text-align: center;
  h1 {
    margin-bottom: 20px;
  }
  p {
    max-width: 500px;
    margin: auto;
  }
`

const Wrap = styled.div`
  background-color: #edebe3;
  padding-top: 66px;
  padding-bottom: 98px;
  padding-left: 5%;
  padding-right: 5%;
  @media(min-width: 1440px) {
    padding-left: 20%;
    padding-right: 20%;
  }
`

export default IndividualProgramSection


